'use client';
import { useEffect, useRef, useState } from 'react';

type Props = {
  text: string;
  clampHeight: string;
};

export default function PostContent({ text, clampHeight }: Props) {
  const pRef = useRef<HTMLParagraphElement>(null); //<p>„Å®Á¥ê„Å•„Åë
  const [isClamped, setIsClamped] = useState(false);
  const [isOpened, setIsOpened] = useState(false);

  useEffect(() => {
    const el = pRef.current; //<p>„ÇíÂèñÂæó
    if (!el) return;

    // ÂÖ®Êñá„ÅÆÈ´ò„Åï Ôºû Ë°®Á§∫„Åó„Å¶„ÅÑ„ÇãÈ´ò„Åï
    setIsClamped(el.scrollHeight > el.clientHeight);
  }, []);

  return (
    <div className="whitespace-pre-wrap">
      {/* „Ç´„Éº„Éâ„ÅÆÈ´ò„ÅïË™øÊï¥„ÄÄÂ±ïÈñã„Åó„Å¶„ÅÑ„Å™„Åë„Çå„Å∞Âõ∫ÂÆö*/}
      <div className={!isOpened ? `${clampHeight}` : ''}>
        <p
          ref={pRef}
          className={`text-sm leading-relaxed wrap-break-word ${isOpened ? '' : 'line-clamp-4'}`}
        >
          {text}
        </p>

        {/* ÂÖ®Êñá„ÅåÁúÅÁï•ÊôÇ„ÅÆ„ÅøË°®Á§∫„Åï„Åõ„Çã */}
        {isClamped && (
          <button
            onClick={(e) => {
              e.preventDefault(); //„Ç´„Éº„Éâ„ÅÆLink„ÇíÈÅ©Áî®„Åó„Å™„ÅÑ
              setIsOpened(!isOpened); //„Éà„Ç∞„É´Âá¶ÁêÜÔºàtrue/falseÂàáÊõøÔºâ
            }}
            className="block mx-auto text-xs mt-2 text-gray-500 hover:underline hover:font-bold"
          >
            {isOpened ? 'Èñâ„Åò„Çã' : 'Á∂ö„Åç„ÇíË¶ã„Çã'}
          </button>
        )}
      </div>
    </div>
  );
}
